@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);

* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

html, body, #root {
  height: 100%;  
}


body {
  display: flex;
  justify-content: center;
  background-color:rgba(96, 36, 165, 0.068);
  background-image: linear-gradient(white 2px, transparent 2px),
  linear-gradient(90deg, white 2px, transparent 2px),
  linear-gradient(rgba(255,255,255,.3) 1px, transparent 1px),
  linear-gradient(90deg, rgba(255,255,255,.3) 1px, transparent 1px);
  background-size: 100px 100px, 100px 100px, 20px 20px, 20px 20px;
  background-position:-2px -2px, -2px -2px, -1px -1px, -1px -1px;
}

body, input, butto {
  font-family: Roboto, sans-serif;
}
#app {
  max-width: 1200px;
  margin: 0 auto;
  padding: 60px 30px;

  display: flex;
  flex-direction: row;
  align-items: flex-start;}



aside {
  width: 320px;
  background: #fff;
  box-shadow: 0 0 1px 0 rgba(0, 0, 0, 0,02);
  border-radius: 3px;
  padding: 30px 20px
}

aside strong {
  font-size: 22px;
  text-align: center;
  display: block;
  color: #20103c;
  margin-bottom: 20px;
  font-weight: bold;
}
main {
  flex: 1 1;
  margin-left: 20px;
}

main ul {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 20px;
  gap: 20px;
  list-style: none;
  
}

@media(max-width: 650px){ 
  main {
    width: 100%;
  }
  
  main ul {
    grid-template-columns: 1fr;
   }
}
@media(max-width: 1000px){
    #app {
      flex-direction: column;
    }

    #app main {
      margin-left: 0;
      margin-top: 30px;
    }
}
li.dev-item{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #FFF;
  box-shadow: 0 0 1px 0 rgba(0, 0, 0, 0,02);
  border-radius: 3px;
  padding: 20px
}

li.dev-item header {
display: flex;
flex-direction: row;
align-items: center;
}

li.dev-item header img{
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

li.dev-item header .user-info{
  margin-left: 10px;
}

.user-info strong {
  display: block;
  font-size: 17px;
  color: #333;
  }

  .user-info span {
    font-size: 13px;
    color: #999;
    margin-top: 2px;
  }

  li.dev-item p{  
    color: #666;
    font-size: 14px;
    line-height: 20px;
    margin: 10px 0
  }


  
  .dev-buttons {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
  }
  
  .dev-buttons button {
    border: 0;
    background: #553AA2;
    padding: 5px;
    border-radius: 3px;
    font-size: 20px;
    color: #fff;
    cursor: pointer;
    border: none;
    -webkit-transition: background 0.1s;
    transition: background 0.1s;
  
    display: flex;
  }

  .dev-buttons :hover {
    background-color: #8e4dff;
    border-radius: 5px;
  }

  



.notValid {
  -webkit-animation: shake 500ms linear;
          animation: shake 500ms linear;
  
}

@-webkit-keyframes shake {
  10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
    opacity: 1;
  }  
  20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
    opacity: 0.9;
  }
  30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
    opacity: 0.8;
  }
  40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
    opacity: 0.7;
  }
  }

@keyframes shake {
  10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
    opacity: 1;
  }  
  20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
    opacity: 0.9;
  }
  30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
    opacity: 0.8;
  }
  40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
    opacity: 0.7;
  }
  }

  aside {
    display: flex;
    flex-direction: column;
    align-items: center;

  }
  aside img {
    height: 100px;
    width: 80%;
    border-bottom: 1px solid #eee;
  }

aside form .input-block + .input-block {
  margin-top: 20px
}

aside form .input-block label{
  color: #8c839b;
  font-size: 14px;
  display: block;
  margin-bottom: 2px;
}



aside form .input-block input {
  width: 100%;
  height: 32px;
  font-size: 14px;
  color: #b3b3b3;
  border: 0;
  border-bottom: 1px solid #eee;
}

aside h1 {
  font-size: 30px;
  margin-bottom: 30px;
  margin-top: 10px;
}

aside form .input-group {
  margin-top: 20px;
  display: grid;
  grid-gap: 20px;
  gap: 20px;
  grid-template-columns: 1fr 1fr;
}

aside form .input-group .input-block {
  margin-top: 0
}


aside form button[type=submit]{
  width: 100%;
  border: 0;
  margin-top: 30px;
  background: #5a2ea6;
  border-radius: 2px;
  padding: 15px 20px;
  font-size: 16px;
  font-weight: bold;
  color: #FFF;
  cursor: pointer;
  -webkit-transition: background 0.5s;
  transition: background 0.5s;
}

aside form button[type=submit]:hover {
  background: #8e4dff;
 
}
@media(max-width: 650px){ 
  aside {
    width: 100%;
  }
 }
@media(max-width: 1000px){

  aside {
    width: 100%
  }

  aside img {
    width: 40%;
  }
  }
